import {ChangeDetectionStrategy, Component, Input} from "@angular/core";
import {FaultDomainModel} from "@model/fault-domain.model";
import {FaultDomainConsumptionData} from "@model/fault-domain-consumption-data";
import {ConnectionState} from "@generated/connection-state";
import {ClrSelectedState} from "@clr/angular";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   selector: "standalone-host-card",
   templateUrl: "standalone-host-card.html",
   styleUrls: ["standalone-host-card.scss", "drag-directive.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class StandaloneHostCardComponent {
   @Input()
   isVsanMaxEnabled: boolean;

   @Input()
   host: FaultDomainModel;

   @Input()
   selection: FaultDomainModel[];

   @Input()
   dropTargets: string[] = [];

   @Input()
   consumption: FaultDomainConsumptionData;

   @Input()
   hostReservedCapacitySupported: boolean;

   toggleHostSelection(): void {
      if (!this.isConnected(this.host)) {
         return;
      }
      if (this.host.isSelected === ClrSelectedState.SELECTED) {
         this.host.isSelected = ClrSelectedState.UNSELECTED;
      } else {
         this.host.isSelected = ClrSelectedState.SELECTED;
      }
   }

   isConnected(host: FaultDomainModel): boolean {
      return (host.data.connectionState === ConnectionState.connected);
   }

   dragStart(): void {
      this.host.isDragging = true;
      this.selection.forEach(model => model.isDragging = true);
   }

   dragEnd(): void {
      this.host.isDragging = false;
      this.selection.forEach(model => model.isDragging = false);
   }

   getNoCapacityLabel(): string {
      return this.isVsanMaxEnabled ? VsanUiUtils.getString("vsan.faultDomains.host.noStoragePoolDisks.label")
            : VsanUiUtils.getString("vsan.faultDomains.host.noDiskGroup.label");
   }
}
