import {Component, EventEmitter, Input, Output} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {VsanUiUtils} from "@util/vsan-util";
import {WitnessHostData} from "@generated/witness-host-data";
import {
   ChangeWitnessHostEvent,
   ConfigureStretchedClusterEvent,
   DisabledStretchedClusterEvent,
   FaultDomainEvent,
} from "../../event/fault-domain.event";
import {DomainOrHostData} from "@generated/domain-or-host-data";

@Component({
   selector: "vsan-fault-domains-config",
   templateUrl: "fault-domain-config.html",
   styleUrls: ["fault-domain-config.scss"],
})
export class FaultDomainConfigComponent {
   @Input()
   displayConfigureButtons: boolean = true;

   @Input()
   witnessInfos: WitnessHostData[];

   @Input()
   isSharedWitnessAllowed: boolean;

   @Input()
   hasEditPermission: boolean = true;

   @Input()
   disableCfgStretchedClusterBtn: boolean = false;

   @Input()
   disableChangeWitnessHostBtnReason: string = "";

   @Input()
   disableReason: string;

   @Input()
   isStretchedCluster: boolean;

   @Input()
   faultToleranceInfo: string;

   @Output()
   onActionEvent: EventEmitter<FaultDomainEvent> = new EventEmitter<FaultDomainEvent>();

   constructor(private navigationService: NavigationService) {
   }

   onConfigureStretchedCluster(): void {
      this.onActionEvent.emit(new ConfigureStretchedClusterEvent());
   }

   get configureStretchedClusterBtnTitle(): string {
      if (!this.hasEditPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      if (this.disableCfgStretchedClusterBtn) {
         return this.disableReason;
      }
      return "";
   }

   onDisableStretchedCluster(): void {
      this.onActionEvent.emit(new DisabledStretchedClusterEvent());
   }

   onChangeWitness(): void {
      this.onActionEvent.emit(new ChangeWitnessHostEvent());
   }

   async navigateToHost(hostRef: ManagedObject): Promise<any> {
      // Navigate to "Two Host Clusters" view  if the cluster is
      // two-host type and sharedwitness capability is enabled on Vc and host
      if (this.isSharedWitnessAllowed) {
         this.navigationService.navigateTo(PlatformExtension.HOST_SHARED_WITNESS_CLUSTERS_LIST, hostRef);
         return;
      }
      this.navigationService.navigateTo(PlatformExtension.LAST_VISITED_PAGE, hostRef);
   }
}
