import {ChangeDetectionStrategy, Component, EventEmitter, Input, Output} from "@angular/core";
import {ClrSelectedState} from "@clr/angular";
import {Icon} from "@util/icon";
import {ConnectionState} from "@generated/connection-state";
import {FaultDomainModel} from "@model/fault-domain.model";
import {FaultDomainConsumptionData} from "@model/fault-domain-consumption-data";
import {FaultDomainEvent, MoveHostsEvent} from "../../event/fault-domain.event";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   selector: "fault-domain-card",
   templateUrl: "fault-domain-card.html",
   styleUrls: ["fault-domain-card.scss", "drag-directive.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FaultDomainCardComponent {

   readonly Icon: typeof Icon = Icon;

   @Input()
   isVsanMaxEnabled: boolean;

   @Input()
   model: FaultDomainModel;

   @Input()
   storageConsumption: any;

   @Input()
   hostReservedCapacitySupported: boolean;

   @Output()
   readonly onActionEvent: EventEmitter<FaultDomainEvent> = new EventEmitter<FaultDomainEvent>();

   get selection(): FaultDomainModel[] {
      if (!this.model.hosts) {
         return [];
      }
      return this.model.hosts.filter(host => host.isSelected === ClrSelectedState.SELECTED);
   }

   hasCapacity(model: FaultDomainModel): boolean {
      return !!(this.getDomainConsumption(model).totalBytes);
   }

   toggleHostSelection(host: FaultDomainModel, event: any): void {
      if (this.isConnected(host)) {
         if (host.isSelected === ClrSelectedState.SELECTED) {
            host.isSelected = ClrSelectedState.UNSELECTED;
         } else {
            host.isSelected = ClrSelectedState.SELECTED;
         }
      }
   }

   isConnected(host: FaultDomainModel): boolean {
      return (host.data.connectionState === ConnectionState.connected);
   }

   dropHost(event: any): void {
      const host: FaultDomainModel = event.dragData.host;
      const affected: FaultDomainModel[] = event.dragData.selection;
      if (affected.indexOf(host) === -1) {
         affected.push(host);
      }
      if (affected.length > 0) {
         affected.forEach(affectedHost => affectedHost.isDragging = false);

         const evt: MoveHostsEvent = new MoveHostsEvent();
         evt.hosts = affected;
         evt.targetDomain = this.model.data.uid;
         this.onActionEvent.emit(evt);
      }
   }

   dragStart(): void {
      this.selection.forEach(model => model.isDragging = true);
   }

   dragEnd(): void {
      this.model.hosts.forEach(model => model.isDragging = false);
   }

   getDomainConsumption(model: FaultDomainModel): FaultDomainConsumptionData {
      return FaultDomainConsumptionData.getDomainConsumptionData(model, this.storageConsumption);
   }

   getNoCapacityLabel(): string {
      return this.isVsanMaxEnabled ? VsanUiUtils.getString("vsan.faultDomains.host.noStoragePoolDisks.label")
            : VsanUiUtils.getString("vsan.faultDomains.host.noDiskGroup.label");
   }
}
