import {Component, Input, OnDestroy, OnInit} from "@angular/core";
import {FaultDomainConsumptionData} from "@model/fault-domain-consumption-data";
import {ChartSectionData} from "@component/chart/model/chart-section.data";
import {VsanUiUtils} from "@util/vsan-util";
import {Subscription} from "rxjs";
import {HighchartsThemeService} from "@component/chart/highcharts-theme.service";

@Component({
   selector: "fault-domain-consumption",
   templateUrl: "fault-domain-consumption.html",
   styleUrls: ["fault-domain-consumption.scss"],
})
export class FaultDomainConsumptionComponent implements OnInit, OnDestroy {

   @Input()
   consumption: FaultDomainConsumptionData;

   @Input()
   hostReservedCapacitySupported: boolean;

   @Input()
   hasLabel: boolean = true;

   sectionsData: ChartSectionData[] = [];

   private themeChangedSubscribtion: Subscription;

   constructor(private highchartsThemeService: HighchartsThemeService) {
      this.themeChangedSubscribtion = this.highchartsThemeService.whenThemeChanged.subscribe(
            theme => this.updateData());
   }

   ngOnInit(): void {
      this.updateData();
   }

   ngOnDestroy(): void {
      this.themeChangedSubscribtion.unsubscribe();
   }

   private updateData = () => {
      this.sectionsData = [];
      this.sectionsData.push(new ChartSectionData(
            VsanUiUtils.getString("vsan.faultDomains.card.capacity.label"),
            this.consumption.usedBytes,
            "faultDomainUsed",
            true,
            "", // Empty string means use Blue palette
            this.getTooltipData(),
      ));
   };

   private getTooltipData(): Map<string, string> {
      const result: Map<string, string> = new Map();
      if (this.consumption.usedBytes > 0) {
         result.set(VsanUiUtils.getString("vsan.faultDomains.card.capacity.used"),
               VsanUiUtils.formatFileSize(this.consumption.usedBytes));
      }
      if (this.consumption.reservedBytes > 0) {
         result.set(VsanUiUtils.getString("vsan.faultDomains.card.capacity.reserved"),
               VsanUiUtils.formatFileSize(this.consumption.reservedBytes));
      }
      if (this.consumption.freeBytes > 0) {
         result.set(VsanUiUtils.getString("vsan.faultDomains.card.capacity.free"),
               VsanUiUtils.formatFileSize(this.consumption.freeBytes));
      }
      result.set(VsanUiUtils.getString("vsan.faultDomains.card.capacity.total"),
            VsanUiUtils.formatFileSize(this.consumption.totalBytes));
      return result;
   }
}
